export const required = value => {
  if (value) return undefined;
  return "  ";
};

export const OnlyLetters = value => {
  if (!/^[a-zA-Z--\s]*$/.test(value)) return `     `;
  return undefined;
};

export const PasswordCheck = MinValue => value => {
  if (value.length < MinValue)
    return `  ${MinValue} `;
  return undefined;
};
