import { reset } from "redux-form";

const Start = "Start";
const End = "End";
const ErrorProc = "ErrorProc";
const CleanUp = "CleanUp";

//  
const getOne = "getOne";

//  
const GetallUsers = "GetallUsers";

//  
const GetAllTask = "GetAllTask";

//  
const GetMyTask = "GetMyTask";

//     
const DownLinkBoss = "DownLinkBoss";

//     
const DownLinkWorker = "DownLinkWorker";

let initialState = {
  //  
  error: null,

  //   
  loading: false,

  //   
  DataProjects: [],

  //   
  OneProject: [],

  //   (  )
  DataUsers: [],

  //    
  LinkBoss: null,

  //    
  LinkWorker: null
};

const dashboardReducer = (state = initialState, action) => {
  switch (action.type) {
    case Start:
      return {
        ...state,
        loading: true
      };

    case End:
      return {
        ...state,
        loading: false,
        error: null
      };
    case ErrorProc:
      return {
        ...state,
        loading: false,
        error: action.payload
      };
    case CleanUp:
      return {
        ...state,
        error: null,
        loading: false,
        DataProjects: []
      };

    case getOne:
      return {
        ...state,
        OneProject: action.project
      };
    case GetallUsers:
      return {
        ...state,
        DataUsers: action.users
      };
    case GetAllTask:
      return {
        ...state,
        DataProjects: action.data
      };
    case DownLinkBoss:
      return {
        ...state,
        LinkBoss: action.url
      };
    case DownLinkWorker:
      return {
        ...state,
        LinkWorker: action.url
      };

    default:
      return state;
  }
};
// 
export const Clean = () => ({ type: CleanUp });
